#jumboHome{
    height: 100vh;
    background-image: url("./pills2.jpg");
    background-size: 1924px 1263px;
    background-position-x: 12%;
    background-position-y: 88%;
    margin: 0px;
}

#heading{
    font-family: 'Inknut Antiqua', serif;
    color: #FFF;
    font-size: 620%;
    margin-top:5%;
    padding: 0px;
}

#subheading{
    color: #FFF;
    margin-left: 4%;
    font-family: 'Raleway', sans-serif;
}

.btn-home {
    height: 105px;
    width: 160px;
    margin: 50px;
    font-size: 30px;
}

.img{
    float:right;
    
}

#logoHome{
    height: 100px;
    width: 75px;
    margin-bottom: 35px;
}

.modal-fade{
    width: 120%;
}


.modal-content{
    background: rgba(255,255,255);
    font-family: 'Raleway', sans-serif;
}




